body {
  margin: 0;
}
.mainInfo {
  width: 50%
}
.contactInfo{
  text-align: right;
  width: 50%;
}
@media screen and (max-width: 650px) {
  .displayContainer{
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .mainInfo{
    width: 100%
  }
  .contactInfo{
    width: 100%;
    text-align: center;
  }
}